.AppTime {
  position: fixed;
  font-size: 1.3em;
  background-color: #3b3733;
  left: 50%;
  transform: translate(-50%, 0px);
  color: #fff;
  padding: 8px 10px;
  border-radius: 0px 0px 10px 10px;
  box-shadow: 0px 0px 7px 1px rgb(0 0 0 / 40%);

  text-align: center;
}

.AppTimeTicker {
  background-color: #5a514a;
  border: none;
  padding: 2px 6px;
  color: #fff;
  width: 100%;
  border-radius: 0px 0px 4px 4px;
}
